import React, { useEffect, useState } from 'react';
import stylesDropdown from './dropdown.css';
import { usePopper } from 'react-popper';

interface IDropdownProps {
    button: React.ReactNode;
    children: React.ReactNode;
    isOpen?: boolean;
    onOpen?: () => void;
    onClose?: () => void;
}

const NOOP = () => {};

export function Dropdown({
    button,
    children,
    isOpen,
    onOpen = NOOP,
    onClose = NOOP,
}: IDropdownProps) {
    const [isDropdownOpen, setIsDropdownOpen] = useState(false);

    const handleOpen = (e: React.MouseEvent) => {
        if (isOpen === undefined) {
            setIsDropdownOpen(!isDropdownOpen);
        }

        e.stopPropagation();
    };

    useEffect(() => {
        setIsDropdownOpen(isOpen ?? false);
    }, [isOpen]);
    useEffect(() => (isDropdownOpen ? onOpen() : onClose()), [isDropdownOpen, onOpen, onClose]);

    const [referenceElement, setReferenceElement] = useState<HTMLDivElement | null>(null);
    const [popperElement, setPopperElement] = useState<HTMLDivElement | null>(null);
    const { styles, attributes } = usePopper(referenceElement, popperElement, {});
    useEffect(() => {
        if (isOpen !== undefined || !isDropdownOpen) {
            return;
        }

        function handleClick(event: MouseEvent) {
            if (event.target instanceof Node && !popperElement?.contains(event.target)) {
                setIsDropdownOpen(false);
            }
        }

        document.addEventListener('click', handleClick);

        return () => document.removeEventListener('click', handleClick);
    }, [isOpen, isDropdownOpen, popperElement]);

    return (
        <div className={stylesDropdown.container}>
            <div onClick={handleOpen} ref={setReferenceElement}>
                {button}
            </div>
            {isDropdownOpen && (
                <div
                    onClick={() => setIsDropdownOpen(false)}
                    style={{ ...styles.popper, zIndex: 1000 }}
                    ref={setPopperElement}
                    {...attributes.popper}>
                    {children}
                </div>
            )}
        </div>
    );
}
