import { Text } from '../../../UI/Text';
import { Button } from '../../../UI/Button';
import { EIcons, Icon } from '../../../UI/Icons';
import React from 'react';
import styles from './deletetaskmodal.module.css';
import { Modal } from '../../../UI/Modal';
import { EColor } from '../../../UI/types';

interface IDeleteTaskModal {
    isOpen: boolean;
    onDelete: () => void;
    onCancel: () => void;
}

export function DeleteTaskModal({ isOpen, onDelete, onCancel }: IDeleteTaskModal) {
    return (
        <Modal isOpen={isOpen}>
            <div className={styles.content}>
                <Text size={24}> ?</Text>

                <Button type="alarm" onClick={onDelete}>
                    
                </Button>

                <button className={styles.buttonCross} onClick={onCancel}>
                    <Icon iconName={EIcons.Cross} color={EColor.GreyC4} />
                </button>

                <Text size={16} additionalClassName={styles.spanCancel} onClick={onCancel}>
                    
                </Text>
            </div>
        </Modal>
    );
}
