import { Reducer } from 'redux';
import {
    SET_TOTAL_SECONDS,
    TIMER_DONE,
    TIMER_PAUSE,
    TIMER_PROLONG,
    TIMER_RESUME,
    TIMER_SKIP,
    TIMER_START,
    TIMER_STOP,
    TimerActions,
} from '../actions/timer';
import {
    selectCanPause,
    selectCanProlongTime,
    selectCanResume,
    selectCanSkip,
    selectCanStart,
    selectCanStop,
} from '../selectors/timer';
import { configuration } from '../configuration';
import { TimerStatus, TimerType } from '../types';
import { assoc } from '../../utils/assoc';
import addSeconds from 'date-fns/addSeconds';

export type TimerState = {
    totalSeconds: number;
    status: TimerStatus;
    type: TimerType;
    shortBreakCount: number;
    pomidorMumber: number;
    estimatedCompletionTime: Date | null;
};

export const initialTimerState: TimerState = {
    totalSeconds: configuration.PomidorDurationMinutes * 60,
    status: TimerStatus.NotStarted,
    type: TimerType.Pomidor,
    shortBreakCount: 0,
    pomidorMumber: 1,
    estimatedCompletionTime: null,
};

function getTotalSeconds(type: TimerType): number {
    switch (type) {
        case TimerType.ShortBreak:
            return configuration.ShortBreakDurationMinutes * 60;
        case TimerType.LongBreak:
            return configuration.LongBreakDurationMinutes * 60;
        default:
            return configuration.PomidorDurationMinutes * 60;
    }
}

const assocTotalSeconds = (state: TimerState, totalSeconds: number) =>
    assoc('totalSeconds', totalSeconds)(state);
const assocStatus = (state: TimerState, status: TimerStatus) => assoc('status', status)(state);

function assocNextState(state: TimerState) {
    let newType: TimerType = TimerType.Pomidor;
    let newShortBreakCount = state.shortBreakCount;
    switch (state.type) {
        case TimerType.Pomidor:
            if (state.shortBreakCount === 4) {
                newType = TimerType.LongBreak;
                newShortBreakCount = 0;
            } else {
                newType = TimerType.ShortBreak;
                newShortBreakCount++;
            }
            break;
        case TimerType.ShortBreak:
        case TimerType.LongBreak:
            newType = TimerType.Pomidor;
            break;
    }
    const newTotalSeconds = getTotalSeconds(newType);

    return {
        ...assocTotalSeconds(state, newTotalSeconds),
        type: newType,
        shortBreakCount: newShortBreakCount,
    };
}

export const timerReducer: Reducer<TimerState, TimerActions> = (
    state = initialTimerState,
    action
) => {
    switch (action.type) {
        case TIMER_START: {
            if (!selectCanStart(state)) {
                return state;
            }
            return {
                ...state,
                status: TimerStatus.Started,
                estimatedCompletionTime: addSeconds(new Date(), state.totalSeconds),
            };
        }
        case TIMER_RESUME: {
            if (!selectCanResume(state)) {
                return state;
            }
            return {
                ...state,
                status: TimerStatus.Started,
                estimatedCompletionTime: addSeconds(new Date(), state.totalSeconds),
            };
        }
        case TIMER_STOP:
            if (!selectCanStop(state)) {
                return state;
            }
            return {
                ...state,
                totalSeconds: configuration.PomidorDurationMinutes * 60,
                status: TimerStatus.NotStarted,
                estimatedCompletionTime: null,
            };
        case TIMER_DONE: {
            return {
                ...assocNextState(state),
                pomidorMumber: state.pomidorMumber + 1,
                status: TimerStatus.NotStarted,
                estimatedCompletionTime: null,
            };
        }
        case TIMER_PAUSE:
            if (!selectCanPause(state)) {
                return state;
            }
            return assocStatus(state, TimerStatus.OnPause);
        case TIMER_SKIP: {
            if (!selectCanSkip(state)) {
                return state;
            }
            return {
                ...assocNextState(state),
                status: TimerStatus.NotStarted,
                estimatedCompletionTime: null,
            };
        }
        case TIMER_PROLONG:
            if (!selectCanProlongTime(state)) {
                return state;
            }

            return {
                ...state,
                totalSeconds: state.totalSeconds + action.countSeconds,
                estimatedCompletionTime: addSeconds(
                    state.estimatedCompletionTime!,
                    action.countSeconds
                ),
            };
        case SET_TOTAL_SECONDS:
            if (state.status !== TimerStatus.Started) {
                return state;
            }
            return assocTotalSeconds(state, action.countSeconds);
        default:
            return state;
    }
};
