import React from 'react';
import styles from './buttonsarea.module.css';
import { Button } from '../../../UI/Button';

interface IButtonsAreaProps {
    onStart?: () => void;
    onStop?: () => void;
    onResume?: () => void;
    onSkip?: () => void;
    onPause?: () => void;
    onDone?: () => void;
    onProlongTime?: () => void;
}

export function ButtonsArea({
    onStart,
    onStop,
    onResume,
    onSkip,
    onPause,
    onDone,
}: IButtonsAreaProps) {
    return (
        <div className={styles.buttonsArea}>
            {onStart && <Button onClick={onStart}></Button>}
            {onPause && (
                <Button onClick={onPause} type="alarm">
                    
                </Button>
            )}
            {onStop && (
                <Button onClick={onStop} type="alarm">
                    
                </Button>
            )}
            {onResume && <Button onClick={onResume}></Button>}
            {onSkip && <Button onClick={onSkip}></Button>}
            {onDone && <Button onClick={onDone}></Button>}
        </div>
    );
}
