import React from 'react';
import { EIcons, Icon } from '../../../../../UI/Icons';
import { Text } from '../../../../../UI/Text';
import { EColor } from '../../../../../UI/types';

interface IMenuItemProps {
    iconName: EIcons;
    text: string;
}

export function MenuItem({ iconName, text }: IMenuItemProps) {
    return (
        <>
            <Icon iconName={iconName} color={EColor.LightGreen} />
            <Text color={EColor.Grey99} weight="light">
                {text}
            </Text>
        </>
    );
}
