import React, { ReactNode } from 'react';
import { EIcons } from './types';
import { MinusInsideCircleIcon } from './units/MinusIcon';
import { TrashIcon } from './units/TrashIcon';
import { PlusInsideCircleIcon } from './units/PlusInsideCircleIcon';
import { PlusIcon } from './units/PlusIcon';
import classNames from 'classnames';
import styles from './icon.module.css';
import { ArrowUpIcon } from './units/ArrowUpIcon';
import { ArrowDownIcon } from './units/ArrowDownIcon';
import { CrossIcon } from './units/CrossIcon';
import { PenIcon } from './units/PenIcon';
import { EllipsisIcon } from './units/EllipsisIcon';
import { DiagrammIcon } from './units/DiagrammIcon';
import { EColor } from '../types';
import { FocusIcon } from './units/FocusIcon';
import { WatchIcon } from './units/WatchIcon';
import { StopIcon } from './units/StopIcon';

interface IIconProps {
    iconName: EIcons;
    color?: EColor;
}

function getIconContent(iconName: EIcons): ReactNode {
    switch (iconName) {
        case EIcons.MinusInsideCircle:
            return <MinusInsideCircleIcon />;
        case EIcons.Trash:
            return <TrashIcon />;
        case EIcons.PlusInsideCircle:
            return <PlusInsideCircleIcon />;
        case EIcons.Plus:
            return <PlusIcon />;
        case EIcons.ArrowUp:
            return <ArrowUpIcon />;
        case EIcons.ArrowDown:
            return <ArrowDownIcon />;
        case EIcons.Cross:
            return <CrossIcon />;
        case EIcons.Pen:
            return <PenIcon />;
        case EIcons.Ellipsis:
            return <EllipsisIcon />;
        case EIcons.Diagramm:
            return <DiagrammIcon />;
        case EIcons.Focus:
            return <FocusIcon />;
        case EIcons.Watch:
            return <WatchIcon />;
        case EIcons.Stop:
            return <StopIcon />;
    }
}

const strokeIcons: EIcons[] = [
    EIcons.ArrowUp,
    EIcons.ArrowDown,
    EIcons.Focus,
    EIcons.Watch,
    EIcons.Stop,
];

export function Icon({ iconName, color }: IIconProps) {
    return (
        <svg
            className={classNames({
                [styles[`${color ?? EColor.White}-fill`]]:
                    color && iconName !== EIcons.ArrowUp && iconName !== EIcons.ArrowDown,
                [styles[`${color ?? EColor.White}-stroke`]]:
                    color && strokeIcons.some(value => iconName === value),
                [styles.stroke]: color && strokeIcons.some(value => iconName === value),
                [styles.largeRound]:
                    iconName === EIcons.Focus ||
                    iconName === EIcons.Watch ||
                    iconName === EIcons.Stop,
                [styles.ellipsis]: iconName === EIcons.Ellipsis,
                [styles.diagramm]: iconName === EIcons.Diagramm,
                [styles.icon]: iconName !== EIcons.Ellipsis && iconName !== EIcons.Diagramm,
            })}>
            {getIconContent(iconName)}
        </svg>
    );
}
