import React, { ReactNode } from 'react';
import classNames from 'classnames';
import styles from './iconcontainer.module.css';
import { EIcons, Icon } from '../../../UI/Icons';

interface IImageProps {
    type: 'focus' | 'pauseTime' | 'numberOfStops';
    disabled?: boolean;
}

function getImageContent(type: 'focus' | 'pauseTime' | 'numberOfStops'): ReactNode {
    switch (type) {
        case 'focus':
            return <Icon iconName={EIcons.Focus} />;
        case 'pauseTime':
            return <Icon iconName={EIcons.Watch} />;
        case 'numberOfStops':
            return <Icon iconName={EIcons.Stop} />;
    }
}

export function IconContainer({ type, disabled = false }: IImageProps) {
    return (
        <div
            className={classNames(styles.image, {
                [styles.focus]: type === 'focus',
                [styles.pauseTime]: type === 'pauseTime',
                [styles.numberOfStops]: type === 'numberOfStops',
                [styles.disabled]: disabled,
            })}>
            {getImageContent(type)}
        </div>
    );
}
