import React from 'react';
import styles from './workinfocard.module.css';
import { Text } from '../../UI/Text';
import { secondsToMinutes } from '../../store/utils/statistics-api';
import { EColor } from '../../UI/types';

export interface IWorkInfoCardProps {
    nameDay: string | null;
    count: number | null;
}

export function WorkInfoCard({ nameDay, count }: IWorkInfoCardProps) {
    const messageReactNode =
        count === null ? (
            <Text> </Text>
        ) : (
            <span>
                <Text>      </Text>
                <Text color={EColor.Red}>{`${secondsToMinutes(count)} `}</Text>
            </span>
        );
    return (
        <div className={styles.wrapper}>
            <div className={styles.content}>
                <Text size={24} weight="bold">
                    {nameDay ?? ' '}
                </Text>
                {messageReactNode}
            </div>
        </div>
    );
}
