import React from 'react';
import { DraggableItem } from './DraggableItem';
import { IItem } from './types';
import { HTML5Backend } from 'react-dnd-html5-backend';
import { DndProvider } from 'react-dnd';

interface IGenericListProps {
    list: IItem[];
    moveItem: (dragIndex: number, hoverIndex: number) => void;
}

const NOOP = () => {};

function GenericDraggableListImpl({ list, moveItem }: IGenericListProps) {
    return (
        <>
            {list.map(({ className, onClick = NOOP, id, content }, index) => (
                <DraggableItem
                    id={id}
                    key={id}
                    content={content}
                    index={index}
                    moveItem={moveItem}
                    className={className}
                    onClick={onClick}
                />
            ))}
        </>
    );
}

export function GenericDraggableList({ list, moveItem }: IGenericListProps) {
    return (
        <DndProvider backend={HTML5Backend}>
            <GenericDraggableListImpl list={list} moveItem={moveItem} />
        </DndProvider>
    );
}
