import React from 'react';
import styles from './instruction.module.css';
import { Text } from '../../UI/Text';

export function Instruction() {
    return (
        <div className={styles.wrapper}>
            <Text size={24} weight="bold">
                !    :
            </Text>
            <ul className={styles.list}>
                <li>      </li>
                <li>  ()</li>
                <li>    </li>
                <li>   (3-5 )</li>
                <li>
                        ,     .
                     4     (15-30 )
                </li>
            </ul>
        </div>
    );
}
