import React from 'react';
import styles from './header.module.css';
import { TimerStatus } from '../../../store/types';
import classNames from 'classnames';
import { EColor } from '../../../UI/types';
import { Text } from '../../../UI/Text';

interface IHeaderProps {
    status: TimerStatus;
    title: string;
    taskName: string;
}

export function Header({ status, title, taskName }: IHeaderProps) {
    return (
        <div
            className={classNames(styles.header, {
                [styles.started]: status === TimerStatus.Started,
                [styles.pause]: status === TimerStatus.OnPause,
            })}>
            <Text weight="bold" color={EColor.White}>
                {taskName}
            </Text>
            <Text color={EColor.White}>{title}</Text>
        </div>
    );
}
