import React from 'react';
import styles from './pomidorinfocard.module.css';
import { Text } from '../../UI/Text';
import { EColor } from '../../UI/types';
import lazyPomidor from '../../assets/lazy_pomidor.png';
import hardworkingPomidor from '../../assets/hardworking_pomidor.png';

export interface IPomidorInfoProps {
    count: number | null;
}

export function PomidorInfoCard({ count }: IPomidorInfoProps) {
    if (!count) {
        return (
            <div className={styles.wrapper}>
                <img src={lazyPomidor} className={styles.lazyPomidor} alt="no file" />
            </div>
        );
    }

    return (
        <div className={styles.wrapper}>
            <div className={styles.topContent}>
                <img src={hardworkingPomidor} alt="no-file" />
                <Text size={24} weight="bold" color={EColor.Grey99}>{`x ${count}`}</Text>
            </div>
            <div className={styles.bottomContent}>
                <Text
                    size={24}
                    color={EColor.White}
                    weight="bold"
                    additionalClassName={styles.bottomText}>
                    {`${count} `}
                </Text>
            </div>
        </div>
    );
}
