import React from 'react';
import styles from './additionalinfocard.module.css';
import classNames from 'classnames';
import { IconContainer } from './IconContainer';
import { getHoursAndMinutes } from '../../utils/date-api';
import { Text } from '../../UI/Text';

export interface IAdditionalInfoCardProps {
    content: number | null;
    type: 'focus' | 'pauseTime' | 'numberOfStops';
}

function formatContent(
    type: 'focus' | 'pauseTime' | 'numberOfStops',
    content: number | null
): string {
    switch (type) {
        case 'focus':
            return `${content ?? 0}%`;
        case 'pauseTime': {
            const [hours, minutes] = getHoursAndMinutes(content ?? 0);
            if (hours === 0) {
                return `${minutes}`;
            }
            return `${hours} ${minutes}`;
        }
        case 'numberOfStops':
            return (content ?? 0).toString();
    }
}

function getHeader(type: 'focus' | 'pauseTime' | 'numberOfStops'): string {
    switch (type) {
        case 'focus':
            return '';
        case 'pauseTime':
            return '  ';
        case 'numberOfStops':
            return '';
    }
}

export function AdditionalInfoCard({ content, type }: IAdditionalInfoCardProps) {
    return (
        <div
            className={classNames(styles.wrapper, {
                [styles.focus]: type === 'focus',
                [styles.pauseTime]: type === 'pauseTime',
                [styles.numberOfStops]: type === 'numberOfStops',
                [styles.disabled]: content === null,
            })}>
            <div className={styles.wrapperContent}>
                <Text size={24} weight="bold">
                    {getHeader(type)}
                </Text>
                <Text size={64}>{formatContent(type, content)}</Text>
            </div>

            <IconContainer type={type} disabled={content === null} />
        </div>
    );
}
