export const ADD_TASK = 'ADD_TASK';

export type AddTaskAction = {
    type: typeof ADD_TASK;
    title: string;
};

export const SELECT_TASK = 'SELECT_TASK';

export type SelectTaskAction = {
    type: typeof SELECT_TASK;
    id: number;
};

export const INC_POMIDORS = 'INC_POMIDORS';

export type IncPomidorsAction = {
    type: typeof INC_POMIDORS;
    idTask: number;
};

export const DEC_POMIDORS = 'DEC_POMIDORS';

export type DecPomidorsAction = {
    type: typeof DEC_POMIDORS;
    idTask: number;
};

export const MOVE_TASK = 'MOVE_TASK';

export type MoveTaskAction = {
    type: typeof MOVE_TASK;
    dragIndex: number;
    hoverIndex: number;
};

export const DELETE_TASK = 'DELETE_TASK';

export type DeleteTaskAction = {
    type: typeof DELETE_TASK;
    idTask: number;
};

export const EDIT_TITLE_TASK = 'EDIT_TITLE_TASK';

export type EditTitleTaskAction = {
    type: typeof EDIT_TITLE_TASK;
    idTask: number;
    newTitle: string;
};

export const COMPLETE_TASK = 'COMPLETE_TASK';

export type CompleteTaskAction = {
    type: typeof COMPLETE_TASK;
    idTask: number;
};

export const DELETE_COMPLETED_TASK = 'DELETE_COMPLETED_TASK';

export type DeleteCompletedTaskAction = {
    type: typeof DELETE_COMPLETED_TASK;
    idTask: number;
};

export type TaskListActions =
    | AddTaskAction
    | SelectTaskAction
    | IncPomidorsAction
    | MoveTaskAction
    | DeleteTaskAction
    | DecPomidorsAction
    | EditTitleTaskAction
    | CompleteTaskAction
    | DeleteCompletedTaskAction;
