import React from 'react';
import styles from './tabloid.module.css';
import { TimerStatus } from '../../../store/types';
import { EIcons, Icon } from '../../../UI/Icons';
import classNames from 'classnames';
import { EColor } from '../../../UI/types';

interface ITabloidProps {
    minutes: number;
    seconds: number;
    status: TimerStatus;
    onProlongTime?: () => void;
}

function numberToStr(n: number): string {
    return n.toLocaleString(undefined, {
        minimumIntegerDigits: 2,
    });
}

export function Tabloid({ minutes, seconds, status, onProlongTime }: ITabloidProps) {
    return (
        <div className={styles.timeWithProlongateContainer}>
            <div className={styles.stubContainer} />

            <div className={styles.timeContainer}>
                <span
                    className={classNames(styles.time, {
                        [styles.startedText]: status === TimerStatus.Started,
                        [styles.pauseText]: status === TimerStatus.OnPause,
                    })}>
                    {`${numberToStr(minutes)} : ${numberToStr(seconds)}`}
                </span>
            </div>

            <div className={styles.stubContainer}>
                {onProlongTime && (
                    <button onClick={onProlongTime} className={styles.prolongTimeButton}>
                        <Icon iconName={EIcons.Plus} color={EColor.White} />
                    </button>
                )}
            </div>
        </div>
    );
}
