import { format } from 'date-fns';
import ru from 'date-fns/locale/ru';
import { endOfWeek, startOfWeek } from 'date-fns';
import differenceInSeconds from 'date-fns/differenceInSeconds';

export function getDayOfWeek(date: Date): number {
    const original = date.getDay();
    if (original === 0) {
        return 6;
    }
    if (original === 6) {
        return 0;
    }

    return original - 1;
}

export function getTodayDayOfWeek(): number {
    return getDayOfWeek(new Date());
}

export function getToday(): Date {
    return getOnlyDate(new Date());
}

export function getOnlyDate(date: Date): Date {
    return new Date(date.getFullYear(), date.getMonth(), date.getDate());
}

export function formatDate(date: Date) {
    return format(date, 'dd.MM.yyyy');
}

export function getHoursAndMinutes(seconds: number): [number, number] {
    if (seconds <= 0) {
        return [0, 0];
    }
    const hours = Math.floor(seconds / 3600);
    const restSeconds = seconds - hours * 3600;
    if (restSeconds === 0) {
        return [hours, 0];
    }

    return [hours, Math.floor(restSeconds / 60)];
}

const options = {
    locale: ru,
    weekStartsOn: 1 as const,
};

export function getStartOfWeek(date: Date): Date {
    return startOfWeek(date, options);
}

export function getEndOfWeek(date: Date): Date {
    return endOfWeek(date, options);
}

export function difInSeconds(left: Date, right: Date) {
    return differenceInSeconds(left, right, { roundingMethod: 'round' });
}
