export const TIMER_START = 'TIMER_START';

export type TimerStartAction = {
    type: typeof TIMER_START;
};

export const TIMER_STOP = 'TIMER_STOP';

export type TimerStopAction = {
    type: typeof TIMER_STOP;
};

export const TIMER_RESUME = 'TIMER_RESUME';

export type TimerResumeAction = {
    type: typeof TIMER_RESUME;
};

export const TIMER_PAUSE = 'TIMER_PAUSE';

export type TimerPauseAction = {
    type: typeof TIMER_PAUSE;
};

export const TIMER_PROLONG = 'TIMER_PROLONG';

export type TimerProlongAction = {
    type: typeof TIMER_PROLONG;
    countSeconds: number;
};

export const TIMER_SKIP = 'TIMER_SKIP';

export type TimerSkipAction = {
    type: typeof TIMER_SKIP;
};

export const TIMER_DONE = 'TIMER_DONE';

export type TimerDoneAction = {
    type: typeof TIMER_DONE;
};

export const SET_TOTAL_SECONDS = 'SET_TOTAL_SECONDS';

export type SetTotalSecondsAction = {
    type: typeof SET_TOTAL_SECONDS;
    countSeconds: number;
};

export const MANUAL_TIMER_DONE = 'MANUAL_TIMER_DONE';

export type ManualTimerDoneAction = {
    type: typeof MANUAL_TIMER_DONE;
};

export type TimerActions =
    | TimerStartAction
    | TimerStopAction
    | TimerPauseAction
    | TimerProlongAction
    | TimerSkipAction
    | TimerDoneAction
    | ManualTimerDoneAction
    | TimerResumeAction
    | SetTotalSecondsAction;
