import React from 'react';
import { Menu } from './Menu';
import { PomidorCountButton } from './PomidorCountButton';
import { TitleTask } from './TitleTask';

export interface ITaskProps {
    id: number;
    title: string;
    isSelected: boolean;
    pomidors: number;
    onIncPomidorsClick: () => void;
    onBlur: () => void;
    onChangeTitle: (value: string) => void;
    onClick: () => void;
    onDeleteRequested: (taskId: number) => void;
}

export function Task({
    title,
    isSelected,
    pomidors,
    onIncPomidorsClick,
    onBlur,
    onChangeTitle,
    id,
    onClick,
    onDeleteRequested,
}: ITaskProps) {
    return (
        <>
            <PomidorCountButton pomidors={pomidors} onClick={onIncPomidorsClick} />
            <TitleTask
                value={title}
                isSelected={isSelected}
                onClickLabel={onClick}
                onBlur={onBlur}
                onChange={value => onChangeTitle(value)}
            />
            <Menu taskId={id} onDeleteRequested={onDeleteRequested} />
        </>
    );
}
