import React from 'react';
import { useSelector } from 'react-redux';
import { RootState } from '../store/reducers';
import { BarChart } from './BarChart';
import {
    selectDayWeekName,
    selectDayStatistics,
    selectTypeWeek,
} from '../store/selectors/statistics';
import { AdditionalInfoContainer } from './AdditionalInfoContainer';
import { PomidorInfoCard } from './PomidorInfoCard';
import { IDayliStatistics, TypeWeek } from '../store/types';
import { SelectWeek } from './SelectWeek';
import { WorkInfoCard } from './WorkInfoCard';
import { Text } from '../UI/Text';
import styles from './statistics.module.css';

export function Statistics() {
    const typeWeek = useSelector<RootState, TypeWeek>(selectTypeWeek);
    const dayliStatistics = useSelector<RootState, IDayliStatistics | null>(selectDayStatistics);
    const nameDay = useSelector<RootState, string | null>(selectDayWeekName);

    return (
        <>
            <div className={styles.wrapper}>
                <div className={styles.header}>
                    <Text size={24} weight="bold">
                         
                    </Text>
                    <SelectWeek typeWeek={typeWeek} />
                </div>
                <div className={styles.middleArea}>
                    <div className={styles.mainInfoArea}>
                        <WorkInfoCard
                            count={dayliStatistics?.timeOfWork ?? null}
                            nameDay={nameDay}
                        />
                        <PomidorInfoCard count={dayliStatistics?.numberPomidors ?? null} />
                    </div>
                    <BarChart />
                </div>
                <AdditionalInfoContainer />
            </div>
        </>
    );
}
