import { difInSeconds } from '../../utils/date-api';

export class CounterTime {
    private secondsCounter: number;
    private startDate: Date | null;

    constructor() {
        this.secondsCounter = 0;
        this.startDate = null;
    }

    reset = () => {
        this.secondsCounter = 0;
        this.startDate = null;
    };

    startWatch = () => {
        this.startDate = new Date();
    };

    stopWatch = () => {
        if (!this.startDate) {
            return;
        }

        const difSeconds = difInSeconds(new Date(), this.startDate);
        this.secondsCounter += Math.max(difSeconds, 0);
        this.startDate = null;
    };

    getSecondsCount = () => this.secondsCounter;
}
