import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../store/reducers';
import { IDayliStatistics } from '../../store/types';
import {
    BarElement,
    CategoryScale,
    Chart as ChartJS,
    ChartOptions,
    Legend,
    LinearScale,
    Title,
    Tooltip,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';
import styles from './barchart.module.css';
import { selectDataSet } from '../../store/selectors/statistics';
import { secondsToMinutes } from '../../store/utils/statistics-api';
import { setDayOfWeek } from '../../store/action-creators/statistics';

ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

const labels = ['', '', '', '', '', '', ''];
const AxisLabelColor = '#999999';
const ActiveAxisLabelColor = '#DC3E22';

const BarColor = '#EA8A79';
const ActiveBarColor = '#DC3E22';

function getColors(normalColor: string, activeColor: string, activeDay: number | null): string[] {
    let colors: string[] = new Array(7);
    colors = colors.fill(normalColor);

    if (activeDay !== null) {
        colors[activeDay] = activeColor;
    }

    return colors;
}

function getAxisXColors(activeDay: number | null): string[] {
    return getColors(AxisLabelColor, ActiveAxisLabelColor, activeDay);
}

function getBarColors(activeDay: number | null): string[] {
    return getColors(BarColor, ActiveBarColor, activeDay);
}

function minutesToString(minutes: number): string {
    try {
        const hours = Math.floor(minutes / 60);
        const restMinutes = Math.round(minutes % 60);
        if (hours === 0) {
            return `${minutes} `;
        } else {
            return `${hours}  ${restMinutes} `;
        }
    } catch {
        return minutes.toString();
    }
}

export function BarChart() {
    const dataSet = useSelector<RootState, IDayliStatistics[]>(selectDataSet);
    const dayOfWeek = useSelector<RootState, number | null>(state => state.statistics.dayWeek);
    const dispatch = useDispatch();

    const options: ChartOptions<'bar'> = {
        responsive: true,
        maintainAspectRatio: false,
        onClick: function (event: any) {
            const value = event.chart.scales.xAxis.getValueForPixel(event.x);
            if (value >= 0 && value <= 6) {
                dispatch(setDayOfWeek(value));
            }
        },
        scales: {
            yAxis: {
                position: 'right',
                ticks: {
                    color: AxisLabelColor,
                    font: {
                        size: 24,
                    },
                    callback: function (value) {
                        if (typeof value === 'number') {
                            return minutesToString(value);
                        }
                        return value;
                    },
                },
            },
            xAxis: {
                ticks: {
                    color: getAxisXColors(dayOfWeek),
                    font: {
                        size: 24,
                    },
                },
            },
        },
        plugins: {
            legend: {
                position: 'top' as const,
                display: false,
            },
            tooltip: {
                callbacks: {
                    label: function (context) {
                        return minutesToString(context.dataset.data[context.dataIndex]);
                    },
                },
            },
        },
    };

    const data = {
        labels,
        datasets: [
            {
                label: ' ()',
                data: dataSet.map(item => secondsToMinutes(item.timeOfWork)),
                backgroundColor: getBarColors(dayOfWeek),
            },
        ],
    };

    return (
        <div className={styles.wrapper}>
            <Bar options={options} data={data} height={470} />
        </div>
    );
}
