import React, { useMemo } from 'react';
import styles from './menu.module.css';
import { Dropdown } from '../../../../UI/Dropdown';
import { GenericList, IItem } from '../../../../UI/GenericList';
import { useDispatch } from 'react-redux';
import { decPomidor, incPomidor, selectTask } from '../../../../store/action-creators/taskList';
import { EIcons, Icon } from '../../../../UI/Icons';
import { MenuItem } from './MenuItem';
import { EColor } from '../../../../UI/types';

interface IMenuProps {
    taskId: number;
    onDeleteRequested: (taskId: number) => void;
}

function createMenuItem(id: string, icon: EIcons, text: string, onClick: () => void): IItem {
    return {
        As: 'div',
        content: <MenuItem iconName={icon} text={text} />,
        id: id,
        className: styles.menuItem,
        onClick: onClick,
    };
}

export function Menu({ taskId, onDeleteRequested }: IMenuProps) {
    const dispatch = useDispatch();

    const button = (
        <button className={styles.menuButton}>
            <Icon iconName={EIcons.Ellipsis} color={EColor.GreyC4} />
        </button>
    );

    const menuItems: IItem[] = useMemo<IItem[]>(() => {
        return [
            createMenuItem('inc', EIcons.PlusInsideCircle, '', () =>
                dispatch(incPomidor(taskId))
            ),
            createMenuItem('dec', EIcons.MinusInsideCircle, '', () =>
                dispatch(decPomidor(taskId))
            ),
            createMenuItem('pen', EIcons.Pen, '', () => dispatch(selectTask(taskId))),
            createMenuItem('remove', EIcons.Trash, '', () => onDeleteRequested(taskId)),
        ];
    }, [dispatch, taskId, onDeleteRequested]);

    return (
        <Dropdown button={button}>
            <div className={styles.dropdown}>
                <GenericList list={menuItems} />
            </div>
        </Dropdown>
    );
}
