import { Action, ActionCreator } from 'redux';
import {
    SET_TOTAL_SECONDS,
    SetTotalSecondsAction,
    TIMER_DONE,
    TIMER_PAUSE,
    TIMER_PROLONG,
    TIMER_RESUME,
    TIMER_SKIP,
    TIMER_START,
    TIMER_STOP,
    TimerDoneAction,
    TimerPauseAction,
    TimerProlongAction,
    TimerResumeAction,
    TimerSkipAction,
    TimerStartAction,
    TimerStopAction,
} from '../actions/timer';
import { ThunkAction, ThunkDispatch } from 'redux-thunk';
import { RootState } from '../reducers';
import { startTimer, stopTimer } from '../utils/timer-api';
import { completeTask, decPomidor } from './taskList';
import { showDoneTimerNotify } from '../utils/notifications-api';
import { writePomidorCompleteEvent } from './statistics';
import { CounterTime } from '../utils/counter-time';
import { TimerType } from '../types';
import { selectCanDone } from '../selectors/timer';
import { difInSeconds } from '../../utils/date-api';

const currentTimerStatistics = {
    workTimeCounter: new CounterTime(),
    pauseTimeCounter: new CounterTime(),
    countOfPauses: 0,
    reset: function () {
        this.countOfPauses = 0;
        this.workTimeCounter.reset();
        this.pauseTimeCounter.reset();
    },
    start: function () {
        this.workTimeCounter.startWatch();
        this.pauseTimeCounter.stopWatch();
    },
    pause: function () {
        this.workTimeCounter.stopWatch();
        this.pauseTimeCounter.startWatch();
        this.countOfPauses++;
    },
    stopWatch: function () {
        this.workTimeCounter.stopWatch();
        this.pauseTimeCounter.stopWatch();
    },
};

const timerStart: ActionCreator<TimerStartAction> = () => {
    currentTimerStatistics.start();

    return { type: TIMER_START };
};

const timerResume: ActionCreator<TimerResumeAction> = () => {
    currentTimerStatistics.start();

    return { type: TIMER_RESUME };
};

export const timerStop: ActionCreator<TimerStopAction> = () => {
    stopTimer();
    currentTimerStatistics.reset();
    return { type: TIMER_STOP };
};

export const timerProlong: ActionCreator<TimerProlongAction> = (countSeconds: number) => ({
    type: TIMER_PROLONG,
    countSeconds: countSeconds,
});

export const setTotalSeconds: ActionCreator<SetTotalSecondsAction> = (countSeconds: number) => ({
    type: SET_TOTAL_SECONDS,
    countSeconds: countSeconds,
});

export const timerPause: ActionCreator<TimerPauseAction> = () => {
    stopTimer();
    currentTimerStatistics.pause();
    return { type: TIMER_PAUSE };
};

export const timerSkip: ActionCreator<TimerSkipAction> = () => {
    stopTimer();
    currentTimerStatistics.reset();
    return { type: TIMER_SKIP };
};

const timerDone: ActionCreator<TimerDoneAction> = () => {
    return { type: TIMER_DONE };
};

function doneTimer(
    dispatch: ThunkDispatch<RootState, unknown, Action<string>>,
    getState: () => RootState
) {
    currentTimerStatistics.stopWatch();

    let state = getState();

    if (state.timer.type === TimerType.Pomidor) {
        dispatch(
            writePomidorCompleteEvent(
                currentTimerStatistics.workTimeCounter.getSecondsCount(),
                currentTimerStatistics.pauseTimeCounter.getSecondsCount(),
                currentTimerStatistics.countOfPauses
            )
        );
    }

    currentTimerStatistics.reset();

    const prevTypeTimer = state.timer.type;

    dispatch(timerDone());

    showDoneTimerNotify(prevTypeTimer, state.timer.type);

    if (state.taskList.tasks.length > 0) {
        if (state.taskList.tasks[0].pomidors === 1) {
            dispatch(completeTask(state.taskList.tasks[0].id));
        } else {
            dispatch(decPomidor(state.taskList.tasks[0].id));
        }
    }
    stopTimer();
}

export const timerStartAsync: (
    resume: boolean
) => ThunkAction<void, RootState, unknown, Action<string>> =
    resume => async (dispatch, getState) => {
        if (resume) {
            dispatch(timerResume());
        } else {
            dispatch(timerStart());
        }

        console.log(new Date());
        let counter: number = 0;

        startTimer(() => {
            let state = getState();

            const remainingSeconds = difInSeconds(state.timer.estimatedCompletionTime!, new Date());

            counter++;

            console.log(counter + ' - ' + Date() + ' - ' + remainingSeconds);
            if (remainingSeconds <= 0) {
                doneTimer(dispatch, getState);

                return;
            }

            dispatch(setTotalSeconds(remainingSeconds));
        }, 1000);
    };

export const manualTimerDone: () => ThunkAction<void, RootState, unknown, Action<string>> =
    () => async (dispatch, getState) => {
        if (!selectCanDone(getState().timer)) {
            return;
        }
        doneTimer(dispatch, getState);
    };
