import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../store/reducers';
import { NewTaskForm } from '../NewTaskForm';
import { NewTaskFormState } from '../../store/reducers/newTaskFormReducer';
import { changeTitle } from '../../store/action-creators/newTaskForm';
import { addTask } from '../../store/action-creators/taskList';

export function NewTaskFormContainer() {
    const newTaskFormState = useSelector<RootState, NewTaskFormState>(state => state.newTaskForm);
    const dispatch = useDispatch();

    return (
        <NewTaskForm
            title={newTaskFormState.title}
            onTitleChange={value => dispatch(changeTitle(value))}
            onSubmit={() => {
                dispatch(changeTitle(''));
                dispatch(addTask(newTaskFormState.title));
            }}
        />
    );
}
