import React, { useState } from 'react';
import styles from './selectweek.module.css';
import { useDispatch } from 'react-redux';
import { Dropdown } from '../../UI/Dropdown';
import { GenericList, IItem } from '../../UI/GenericList';
import { EIcons, Icon } from '../../UI/Icons';
import { setTypeWeek } from '../../store/action-creators/statistics';
import { TypeWeek } from '../../store/types';
import { Text } from '../../UI/Text';
import { EColor } from '../../UI/types';

interface ISelectWeekProps {
    typeWeek: TypeWeek;
}

function getWeekTitle(type: TypeWeek): string {
    switch (type) {
        case TypeWeek.Current:
            return ' ';
        case TypeWeek.Last:
            return ' ';
        case TypeWeek.BeforeLast:
            return '  ';
    }
}

export function createItems(currentType: TypeWeek, onClick: (id: string) => void): IItem[] {
    const types: TypeWeek[] = [TypeWeek.Current, TypeWeek.Last, TypeWeek.BeforeLast].filter(
        t => t !== currentType
    );

    return types.map<IItem>(type => ({
        As: 'div',
        content: <Text>{getWeekTitle(type)}</Text>,
        id: type,
        className: styles.menuItem,
        onClick: onClick,
    }));
}

export function SelectWeek({ typeWeek }: ISelectWeekProps) {
    const dispatch = useDispatch();
    const [isOpen, setIsOpen] = useState(false);

    const button = (
        <button className={styles.menuButton}>
            <Text>{getWeekTitle(typeWeek)}</Text>
            <Icon iconName={isOpen ? EIcons.ArrowUp : EIcons.ArrowDown} color={EColor.DarkRed} />
        </button>
    );

    const menuItems: IItem[] = createItems(typeWeek, id => dispatch(setTypeWeek(id)));

    return (
        <Dropdown button={button} onOpen={() => setIsOpen(true)} onClose={() => setIsOpen(false)}>
            <div className={styles.dropdown}>
                <GenericList list={menuItems} />
            </div>
        </Dropdown>
    );
}
