import { TimerState } from '../reducers/timerReducer';
import { TimerStatus, TimerType } from '../types';

export const selectCanStart = (state: TimerState) => state.status === TimerStatus.NotStarted;
export const selectCanStop = (state: TimerState) =>
    state.status === TimerStatus.Started && state.type === TimerType.Pomidor;
export const selectCanPause = (state: TimerState) => state.status === TimerStatus.Started;
export const selectCanProlongTime = (state: TimerState) =>
    state.status === TimerStatus.Started || state.status === TimerStatus.OnPause;
export const selectCanResume = (state: TimerState) => state.status === TimerStatus.OnPause;
export const selectCanDone = (state: TimerState) =>
    state.status === TimerStatus.OnPause && state.type === TimerType.Pomidor;
export const selectCanSkip = (state: TimerState) =>
    state.status === TimerStatus.NotStarted ||
    (state.status === TimerStatus.OnPause && state.type !== TimerType.Pomidor) ||
    (state.status === TimerStatus.Started && state.type !== TimerType.Pomidor);
