import React from 'react';
import styles from './timer.module.css';
import { TimerStatus } from '../../store/types';
import { Text } from '../../UI/Text';
import { Header } from './Header';
import { Tabloid } from './Tabloid';
import { ButtonsArea } from './ButtonsArea';

interface ITimerProps {
    minutes: number;
    seconds: number;
    status: TimerStatus;
    onStart?: () => void;
    onStop?: () => void;
    onResume?: () => void;
    onSkip?: () => void;
    onPause?: () => void;
    onDone?: () => void;
    onProlongTime?: () => void;
    title: string;
    taskName: string;
}

export function Timer({
    minutes,
    seconds,
    status,
    onStart,
    onStop,
    onResume,
    onSkip,
    onPause,
    onDone,
    onProlongTime,
    title,
    taskName,
}: ITimerProps) {
    return (
        <div className={styles.wrapper}>
            <Header status={status} title={title} taskName={taskName} />
            <div className={styles.area}>
                <div className={styles.content}>
                    <Tabloid
                        minutes={minutes}
                        seconds={seconds}
                        status={status}
                        onProlongTime={onProlongTime}
                    />
                    <Text>{` - ${taskName}`}</Text>
                    <ButtonsArea
                        onProlongTime={onProlongTime}
                        onDone={onDone}
                        onSkip={onSkip}
                        onStart={onStart}
                        onStop={onStop}
                        onResume={onResume}
                        onPause={onPause}
                    />
                </div>
            </div>
        </div>
    );
}
