import React from 'react';
import styles from './header.module.css';
import { Link } from 'react-router-dom';
import { EIcons, Icon } from '../UI/Icons';
import hardworkingPomidor from '../assets/hardworking_pomidor.png';
import { Text } from '../UI/Text';
import { EColor } from '../UI/types';

export function Header() {
    return (
        <header className={styles.header}>
            <Link to={`${process.env.PUBLIC_URL}/`} className={styles.link}>
                <div className={styles.logoContainer}>
                    <img src={hardworkingPomidor} alt="pomidoro" className={styles.logoImage} />
                    <Text
                        color={EColor.Red}
                        weight="light"
                        size={24}
                        additionalClassName={styles.logoText}>
                        POMODORO_BOX
                    </Text>
                </div>
            </Link>
            <Link to={`${process.env.PUBLIC_URL}/statistics`} className={styles.link}>
                <div className={styles.menuContainer}>
                    <Icon iconName={EIcons.Diagramm} />
                    <Text color={EColor.Red} additionalClassName={styles.menuText}>
                        
                    </Text>
                </div>
            </Link>
        </header>
    );
}
