import React from 'react';
import styles from './pomidorcountbutton.module.css';
import { EColor } from '../../../../UI/types';
import pomidor from '../../../../assets/hardworking_pomidor.png';
import { Text } from '../../../../UI/Text';

interface IMenuProps {
    pomidors: number;
    onClick: () => void;
}

export function PomidorCountButton({ pomidors, onClick }: IMenuProps) {
    return (
        <button title=" " onClick={onClick} className={styles.button}>
            <div className={styles.contentWrapper}>
                <img src={pomidor} alt="pomidor" className={styles.image} />
                <Text additionalClassName={styles.text} color={EColor.White} weight="bold">
                    {pomidors}
                </Text>
            </div>
        </button>
    );
}
