import React from 'react';
import { AdditionalInfoCard } from '../AdditionalInfoCard';
import { useSelector } from 'react-redux';
import { RootState } from '../../store/reducers';
import { IDayliStatistics } from '../../store/types';
import { selectDayStatistics } from '../../store/selectors/statistics';
import { getFocus } from '../../store/utils/statistics-api';
import styles from './additionalinfocontainer.module.css';

export function AdditionalInfoContainer() {
    const dayliStatistics = useSelector<RootState, IDayliStatistics | null>(selectDayStatistics);

    return (
        <div className={styles.wrapper}>
            <AdditionalInfoCard
                content={
                    dayliStatistics === null
                        ? null
                        : getFocus(dayliStatistics.timeOfWork, dayliStatistics.timeOfPause)
                }
                type="focus"
            />
            <AdditionalInfoCard content={dayliStatistics?.timeOfPause ?? null} type="pauseTime" />
            <AdditionalInfoCard
                content={dayliStatistics?.numberOfStops ?? null}
                type="numberOfStops"
            />
        </div>
    );
}
