import React, { useState } from 'react';
import { GenericDraggableList, IItem } from '../../UI/GenericDraggableList';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../store/reducers';
import { TaskListState } from '../../store/reducers/taskListReducer';
import styles from './tasklist.module.css';
import {
    deleteTask,
    editTitleTask,
    incPomidor,
    moveTask,
    selectTask,
} from '../../store/action-creators/taskList';
import classNames from 'classnames';
import { Task } from './Task';
import { DeleteTaskModal } from './DeleteTaskModal';
import { Text } from '../../UI/Text';
import { selectTaskTotalSeconds } from '../../store/selectors/taskList';
import { getHoursAndMinutes } from '../../utils/date-api';
import { EColor } from '../../UI/types';

function getSummaryTimeString(totalSeconds: number): string {
    const [hours, minutes] = getHoursAndMinutes(totalSeconds);
    if (hours === 0) {
        return `${minutes} `;
    }
    return `${hours}  ${minutes} `;
}

export function TaskList() {
    const tasksState = useSelector<RootState, TaskListState>(state => state.taskList);
    const taskTotalSeconds = useSelector<RootState, number>(selectTaskTotalSeconds);

    const dispatch = useDispatch();
    const [deletingTaskId, setDeletingTaskId] = useState<number | null>(null);

    if (tasksState.tasks.length === 0) {
        return (
            <Text As="h4" color={EColor.Black}>
                  
            </Text>
        );
    }

    const items: IItem[] = tasksState.tasks.map(task => ({
        content: (
            <Task
                id={task.id}
                title={task.title}
                isSelected={task.id === tasksState.selectedId}
                pomidors={task.pomidors}
                onIncPomidorsClick={() => dispatch(incPomidor(task.id))}
                onBlur={() => dispatch(selectTask(-1))}
                onDeleteRequested={taskId => setDeletingTaskId(taskId)}
                onChangeTitle={value => {
                    if (value) {
                        dispatch(editTitleTask(task.id, value));
                    }
                }}
                onClick={() => dispatch(selectTask(Number(task.id)))}
            />
        ),
        id: task.id.toString(),
        className: classNames(styles.itemList, {
            [styles.selected]: task.id === tasksState.selectedId,
        }),
    }));

    return (
        <>
            <GenericDraggableList
                list={items}
                moveItem={(dragIndex, hoverIndex) => dispatch(moveTask(dragIndex, hoverIndex))}
            />
            <Text color={EColor.Grey99}>{getSummaryTimeString(taskTotalSeconds)}</Text>
            <DeleteTaskModal
                isOpen={deletingTaskId !== null}
                onDelete={() => {
                    dispatch(deleteTask(deletingTaskId));
                    setDeletingTaskId(null);
                }}
                onCancel={() => setDeletingTaskId(null)}
            />
        </>
    );
}
